export default {
components: {
PopupPicker,
Picker,
Group,
Selector,
Flexbox,
FlexboxItem
},
data() {
return {
value_time: [""],
value_type:[""],

timeList: [
["",
"",
""
]
],
typeList:[
["",
"",
"",
"",
"",
""]
]
};
},
methods: {
onChange() {

this.$emit('select',this.value_time[0],this.value_type[0])
}
}
};



